.assets{
  width: 394px;
  height: 124px;
  .title{
    width: 137px;
    height: 37px;
    line-height: 37px;
    text-align: center;
    font-size: 16px;
    letter-spacing: 1px;
    color: #ffffff;
  }
    .kuang{
     
      margin: 10px 0 0 15px;
      width: 470px;
      height: 69px;
      ul{
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        li{
          position: relative;
          display: inline-block;
          list-style: none;
          width: 115px;
          height: 69px;
          p{
            margin-bottom: 0;
           
            text-align: center;
            font-size: 12px;
            color: #ffffff;
            i{
              font-style: normal;
              font-weight: 700;
              font-size: 22px;
              letter-spacing: 1px;
              color: #00ffff;
            }
          }
          span{
            position: absolute;
            top: 23px;
            left: 20px;
            display: inline-block;
            width: 57px;
            height: 57px;
          }
          h3{
            position: absolute;
            width: 95%;
            height: 25%;
            text-align: center;
            top: 55px;
            left: -4px;
            font-size: 12px;
            letter-spacing: 1px;
            color: #ffffff;
          }
        }
      }
    }

}