.assets {
  width: 394px;
  height: 124px;
}
.assets .title {
  width: 137px;
  height: 37px;
  line-height: 37px;
  text-align: center;
  font-size: 16px;
  letter-spacing: 1px;
  color: #ffffff;
}
.assets .kuang {
  margin: 10px 0 0 15px;
  width: 470px;
  height: 69px;
}
.assets .kuang ul {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.assets .kuang ul li {
  position: relative;
  display: inline-block;
  list-style: none;
  width: 115px;
  height: 69px;
}
.assets .kuang ul li p {
  margin-bottom: 0;
  text-align: center;
  font-size: 12px;
  color: #ffffff;
}
.assets .kuang ul li p i {
  font-style: normal;
  font-weight: 700;
  font-size: 22px;
  letter-spacing: 1px;
  color: #00ffff;
}
.assets .kuang ul li span {
  position: absolute;
  top: 23px;
  left: 20px;
  display: inline-block;
  width: 57px;
  height: 57px;
}
.assets .kuang ul li h3 {
  position: absolute;
  width: 95%;
  height: 25%;
  text-align: center;
  top: 55px;
  left: -4px;
  font-size: 12px;
  letter-spacing: 1px;
  color: #ffffff;
}
